<template>
<v-card>
  <img src="../assets/logo.png"/>
    <v-form
    class=" form mx-5 my-5"
    ref="form"
    lazy-validation
  >
    <v-text-field
      v-model="project"
      label="Proyecto"
      required
    ></v-text-field>
     <v-text-field
      v-model="distance"
      label="Proyecto"
      required
    ></v-text-field>
    <v-btn
      class="mr-4 blue"
      @click="send"
    >
      Buscar
    </v-btn>
  </v-form>
</v-card>
</template>

<script>
import { addFileToStorage } from '../data/storage'
    export default {
        data(){
            return{
                project:'',
                distance: 1000
            }
        },
        methods:{
            send(){
              // var bytes = new Uint8Array([0x48, 0x65, 0x6c, 0x6c, 0x6f, 0x2c, 0x20, 0x77, 0x6f, 0x72, 0x6c, 0x64, 0x21]);
              var CSV = [
                  '"1","Proyecto","Distance"',
                  `"2", ${this.project} , ${this.distance}`,
                ].join('\n');

              var contentType = 'text/csv';

              var csvFile = new Blob([CSV], {type: contentType});
              // const obj = {hello: 'world'};
              // const blob = new Blob([JSON.stringify(obj, null, 2)], {type : 'application/json'});
                addFileToStorage('/data.csv',csvFile)
                .then(function() {
                  alert('Uploaded an array!');
                });
            }
        }
    }
</script>

